import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MatTableDataSource } from "@angular/material/table";
import { IDeleteEmpStatusModel, IEmpJobDataModel } from "app/personnel-setup/models/employee-employment-info";
import { IGUIModel, IRowActionsModel } from "app/shared/interfaces/user-views";
import {
  EmployeeJobInformationService
} from "app/personnel-setup/services/employees/employement-information/employee-job-information.service";
import { MatDialog } from "@angular/material/dialog";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { ViewsManager } from "app/shared/resources/views-manager";

import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPersonInstanceOrg } from "app/org-management/model/org-structure";
import { IEmployeeJobTitleModel } from "app/org-management/model/job-title";
import {
  CloseJobDialogComponent
} from "app/personnel-setup/components/employees/employement-information/emp-job-info/close-job-dialog/close-job-dialog.component";
import {
  AssignAsOrgManagerDialogComponent
} from "app/personnel-setup/components/employees/employement-information/emp-job-info/assign-as-org-manager-dialog/assign-as-org-manager-dialog.component";

export enum ActionType {
  assign = "assign",
  append = "append",
  edit = "edit",
  delete = "delete",
  close = "close",
  assignManager = "assignManager",
}


@Component({
  selector: "app-emp-job-table",
  templateUrl: "./emp-job-table.component.html",
  styleUrls: ["./emp-job-table.component.scss"]
})
export class EmpJobTableComponent extends AppHelpers implements OnChanges {
  @Input() hasUpdate: boolean;
  @Input() hideActions: boolean = false;
  @Input() isMatPaginator: boolean = false;
  @Output() rowToEdit = new EventEmitter<{ status: IEmpJobDataModel, org: IPersonInstanceOrg, job: IEmployeeJobTitleModel }>();
  @Output() afterDelete = new EventEmitter<IEmpJobDataModel>();
  @Output() currentAction = new EventEmitter<ActionType>();
  employeeId: number = 0;
  employeeInstanceId: number = 0;
  displayedColumns: string[] = [
    "status",
    "status_date",
    "status_end_date",
    // "reasons",
    "org",
    // "from_Date",
    // "job",
    // "from_Date",
    // "counted",
    // "job actions",
    // "org actions",
    "actions",
  ];
  dataSource: MatTableDataSource<IEmpJobDataModel>;
  isLoaded: boolean = false;
  jobRowFunctions: IRowActionsModel = {basic: [], dropdown: []};
  orgRowFunctions: IRowActionsModel = {basic: [], dropdown: []};
  statusRowFunctions: IRowActionsModel = {basic: [], dropdown: []};

  constructor(
    private employeeJobInformationService: EmployeeJobInformationService,
    private dialog: MatDialog,
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe(id => this.employeeInstanceId = id);

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_job_info_sec
    );


    // row functions on (job org nested table)
    this.jobRowFunctions.basic = this.rowFunctions.basic.filter(ele => {
      let fun = ele.name.toLocaleLowerCase();
      return fun.includes(ActionType.edit) || fun.includes(ActionType.delete) || fun.includes(ActionType.close);
    });

    // row functions on (org first nested table)
    this.orgRowFunctions.basic = this.rowFunctions.basic.filter((ele) =>
      !this.jobRowFunctions.basic.map((job) => job.id).includes(ele.id)
    );

    // row functions on (status table)
    this.statusRowFunctions.basic = this.rowFunctions.dropdown;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasUpdate && changes.hasUpdate.currentValue && this.employeeInstanceId > 0) this.getList(this.firstPage);
    if (this.hideActions) {
      this.statusRowFunctions.basic = this.statusRowFunctions.basic.filter(e => e.name.toLowerCase() === ActionType.delete);
      this.jobRowFunctions.basic = this.jobRowFunctions.basic.filter(e => e.name.toLowerCase() === ActionType.delete);
    }
  }


  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    this.isLoaded = false;
    if (pageSize) {
      this.page.dialogSize = pageSize;
    }
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeJobInformationService
      .getList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmpJobDataModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        }
        else {
          this.errorList(res?.message?.messages);
        }
      });
  }


  toEdit(status: IEmpJobDataModel, org: IPersonInstanceOrg, job: IEmployeeJobTitleModel) {
    this.rowToEdit.emit({status, org, job});
  }

  //on delete job
  onDeleteJob(status: IEmpJobDataModel, job: IEmployeeJobTitleModel): void {
    let msg = this.msgTranslate(job.core_Job_Title.ar_Name, job.core_Job_Title.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(status, job);
      }
    });
  }


  //on delete status
  onDeleteStatus(status: IEmpJobDataModel): void {
    let msg = this.msgTranslate(status.core_Status.ar_Name, status.core_Status.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(status, null, true);
      }
    });
  }

  delete(status: IEmpJobDataModel, job: IEmployeeJobTitleModel, deleteStatus = false) {
    this.showLoading("table");
    let params: IDeleteEmpStatusModel = {
      employeeStatusID: status.id,
      employeeJobTitleID: job?.id ?? 0,
      DeleteStatusIfOrphan: deleteStatus
    };
    this.employeeJobInformationService
      .Delete(params)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          }
          else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
            this.afterDelete.emit(status);
          }

        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("table");
      });
  }


  openCloseDialog(job: IEmployeeJobTitleModel): void {
  //  let msg = this.msgTranslate(job.core_Job_Title.ar_Name, job.core_Job_Title.en_Name);
    this.dialog
      .open(CloseJobDialogComponent, {
        data: job,
        height: "auto",
        minWidth: "40%",
        autoFocus: false,
        panelClass: "dialog-emp-form",
      })
      .afterClosed()
      .subscribe((data) => {});

  }

  onAssign(status: IEmpJobDataModel) {
    this.currentAction.emit(ActionType.assign);
    this.toEdit(status, null, null);
    // console.log("onAssign", this.currentAction);
  }

  orgOnAction(event: { data; fun: IGUIModel }, status: IEmpJobDataModel, org: IPersonInstanceOrg) {
    if (event.fun.name == ActionType.assignManager) this.openAssignManagerDialog(org);
    if (event.fun.name == ActionType.append) {
      this.currentAction.emit(ActionType.append);
      this.toEdit(status, org, null);

      // console.log("onAppend", this.currentAction);
    }
  }

  jobOnAction(event: { data; fun: IGUIModel }, job: IEmployeeJobTitleModel) {
    if (event.fun.name == "close") this.openCloseDialog(job);
  }

  openAssignManagerDialog(org: IPersonInstanceOrg) {
    this.dialog
      .open(AssignAsOrgManagerDialogComponent, {
        data: org,
        minWidth: "50%",
        height: "auto",
        autoFocus: false,
        panelClass: "dialog-emp-form",
      })
      .afterClosed()
      .subscribe((data) => {});
  }


}
