import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { jobToMap } from "app/org-management/mapper/job-title";
import { IJobTitleModel } from "app/org-management/model/job-title";
import { JobTitleService } from "app/org-management/services/job-profile/job-title.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-jobs-profile-index",
  templateUrl: "./jobs-profile-index.component.html",
  styleUrls: ["./jobs-profile-index.component.scss"],
})
export class JobsProfileIndexComponent extends AppHelpers implements OnInit {
  mandatoryColomns: string[] = ["Ser", "id", "code", "en_Name", "ar_Name", "actions"];
  optionalColomns: string[] = ["jobFamily"];

  dataSource: MatTableDataSource<IJobTitleModel>;
  isLoaded: boolean = false;

  constructor(public jobTitleService: JobTitleService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.removePageParams();
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.jobTitleService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IJobTitleModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(jobToMap(res.data));
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  onDelete(jobProfile: IJobTitleModel): void {
    let msg = this.msgTranslate(jobProfile.ar_Name, jobProfile.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(jobProfile);
    });
  }

  delete(jobProfile: IJobTitleModel) {
    this.showLoading();
    this.jobTitleService.Delete(jobProfile.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }
}
