import { Component, Inject } from "@angular/core";
import { ICustomPolicyResponse, IIntersectionEmployeesModel, IIntersectionEmpRequest } from "app/org-management/model/policy-group";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { PolicyGroupService } from "app/org-management/services/policy-group.service";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MatTableDataSource } from "@angular/material/table";
import { HelpersFunctions, msgTranslate } from "app/shared/resources/helpers-functions";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "app-intersection-emp-dialog",
  templateUrl: "./intersection-emp-dialog.component.html",
  styleUrls: ["./intersection-emp-dialog.component.scss"]
})
export class IntersectionEmpDialogComponent {
  policyStartDate = new FormControl("");
  appHelper: AppHelpers = new AppHelpers();
  selectedEmployees: IIntersectionEmpRequest[] = [];

  dataSource: MatTableDataSource<IIntersectionEmployeesModel>;
  mandatoryColomns: string[] = [
    "employee",
    "code",
    Constants.selectColName,
  ];

  constructor(@Inject(MAT_DIALOG_DATA) public model: ICustomPolicyResponse,
              public dialogRef: MatDialogRef<IntersectionEmpDialogComponent>,
              private policyGroupService: PolicyGroupService) {

    if (model?.intersectionEmployees) {
      model?.intersectionEmployees.map((e: any) => {
        e.employee = msgTranslate(e.employeeNameAR, e.employeeNameEN);
        e.code = e.employeeCode;
      });
      this.dataSource = new MatTableDataSource(model?.intersectionEmployees);
    }
    this.policyStartDate.valueChanges.subscribe(val => {
    });

  }


  submit() {
    if (this.selectedEmployees?.length === 0) {
      this.appHelper.error("mustSelectEmployeeAtLeast", "validation");
      return;
    } else {
      this.appHelper.showLoading();
      this.policyGroupService
        .saveIntersectionEmployees(this.selectedEmployees)
        .subscribe((res: ICustomPolicyResponse) => {
          if (res?.message?.messageType == MessageTypes.Success) {
            this.selectedEmployees = [];
            this.dialogRef.close(true);
            this.appHelper.addSuccessfullyMsg(res?.message?.messageTypeName);
          } else {
            this.appHelper.errorList(res?.message?.messages);
          }
          this.appHelper.hideLoading();
        });
    }  // else this.toastr.error("General.formNotValid");
  }

  onSelectEmployee(model: IIntersectionEmployeesModel[]) {
    const body: IIntersectionEmpRequest[] = [];
    this.dataSource.data.forEach(e => {
      if (e.isSelected) {
        let item: IIntersectionEmpRequest = {
          employeeId: e.employeeId,
          policyGroupId: this.model.data.id,
          policyGroupTypeId: this.model.data.policyGroup_Type_ID,
          policyStartDate: HelpersFunctions.transformData(this.policyStartDate?.value),
        };
        body.push(item);
      }
    });
    this.selectedEmployees = body;
  }

}
