import { msgTranslate } from "app/shared/resources/helpers-functions";
import { IPolicyGroupIndexModel, IPolicyGroupModel } from "../model/policy-group";

export function policyGroupDisplay(data: IPolicyGroupIndexModel[]): IPolicyGroupModel[] {
  return data.map((ele) => {
    return <IPolicyGroupModel>{
      id: ele?.core_PolicyGroup?.id,
      ar_Name: ele?.core_PolicyGroup?.ar_Name,
      en_Name: ele?.core_PolicyGroup?.en_Name,
      policyGroupType: msgTranslate(
        ele?.core_PolicyGroup?.core_PolicyGroup_Type?.ar_Name,
        ele?.core_PolicyGroup?.core_PolicyGroup_Type?.en_Name
      ),
      noOfEmployees: ele.countEmpsOnPolicy
    };
  });
}
